 :root {
   --primary: #141819;
   --secondary: #00a8cd;
   --fourth: #ffffff;
   --fifth: #00d0ff;
   --size: 2em;
 }

 * {
   margin: 0;
   padding: 0;
   box-sizing: border-box;
   text-decoration: none;
   list-style: none;
   color: var(--fourth);
 }

 body {
  border: solid red;
   background-color: var(--primary);
   /* height: 100vh; */
}

/* Headings */
h1, h2, h3 {
  font-family: 'Playfair Display', serif;
  font-weight: 700;
}

p {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.6;
  
  
  /* @media screen and (max-width: 480px) {
    font-size: 13px;
    line-height: 1.4;
  } */
}


/* Buttons */
button {
  border: solid green;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  height: 48px;
  padding: 0 20px; 
  border-radius: 6px; 
  /* border: none;  */
  cursor: pointer;
  color: var(--fourth);
  background-color: var(--secondary);
  text-transform: uppercase;
  transition: background-color 0.3s ease; 
}

button:hover {
  background-color: var(--fifth);
}
  .nav-bar {
    border: solid yellow;
